import React, { useRef, useState } from 'react';
import './App.css';

function App() {
  const inputRef = useRef(null); 
  const resultRef = useRef(null); 
  const [result, setResult] = useState(0); 

  function plus(e) { 
    e.preventDefault(); 
    setResult((result) => result + Number(inputRef.current.value)); 
  }; 
  function minus(e) { 
    e.preventDefault(); 
    setResult((result) => result - Number(inputRef.current.value));
  };
 
  function times(e) { 
    e.preventDefault(); 
    setResult((result) => result * Number(inputRef.current.value));
  }; 
 
  function divide(e) { 
    e.preventDefault(); 
    setResult((result) => result / Number(inputRef.current.value));
  };
 
  function resetInput(e) { 
    e.preventDefault();
    inputRef.current.value = null;
  }; 
 
  function resetResult(e) { 
    e.preventDefault();
    setResult(null);
  }; 
  return (
    <div className="App">
      <div> 
        <h1>Simplest Working Calculator</h1> 
      </div> 
      <form> 
        <p ref={resultRef}> 
          {result} 
        </p>
        <br/>
        <input
          pattern="[0-9]" 
          ref={inputRef} 
          type="number" 
          placeholder="Type a number"
          className="in"
        />
        <div>
          <button onClick={plus}>add</button>
          <button onClick={minus}>sub</button>
          <button onClick={times}>mul</button>
          <button onClick={divide}>div</button>
        </div>
        <button onClick={resetInput} className="reset input">resetInput</button>
        <button onClick={resetResult} className="reset result">resetResult</button>
      </form> 
    </div>
  );
}

export default App;




